import Vue from 'vue'
import VueRouter from 'vue-router'

import config from './../config/config'

// 引入主面板
import LayOut from './../views/layout/LayOut'

Vue.use(VueRouter);

// 修改router(路由)的push方法, 阻止重复点击报错
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err=>err);
};

const originalReplace = VueRouter.prototype.replace;
VueRouter.prototype.replace = function replace(location) {
    return originalReplace.call(this, location).catch(err=>err);
};

const routes = [
    {path: '/login', name: 'login', component: () => import('@/views/login/index')},
    {
        path: '/',
        name: 'layOut',
        component: LayOut,
        redirect: '/home',
        children: [
            {path: 'home', name: 'home', component: () => import('@/views/home/index')}
        ]
    },
    {
        path: '/pm',
        component: LayOut,
        redirect: '/pm/product',
        children: [
            {path: 'product', name: 'product', component: () => import('@/views/pm/product/index')},
            {path: 'addProduct', name: 'add-product', component: () => import('@/views/pm/product/add')},
            {path: 'updateProduct', name: 'update-product', component: () => import('@/views/pm/product/update')},
            {path: 'productCategory', name: 'product-category', component: () => import('@/views/pm/productCategory/index')},
            {path: 'productCateAdd', name: 'product-category-add', component: () => import('@/views/pm/productCategory/add')},
            {path: 'productCateUpdate', name: 'product-category-update', component: () => import('@/views/pm/productCategory/update')}
        ]
    },
    {
        path: '/om',
        component: LayOut,
        redirect: '/om/order',
        children: [
            {path: 'order', name: 'order', component: () => import('@/views/om/index')},
            {path: 'setting', name: 'setting', component: () => import('@/views/om/setting')},
            {path: 'deliver', name: 'deliver', component: () => import('@/views/om/deliverList')},
            {path: 'detail', name: 'detail', component: () => import('@/views/om/detail')},
        ]
    },
    {
        path: '/um',
        component: LayOut,
        redirect: '/um/admin',
        children: [
            {path: 'admin', name: 'admin', component: () => import('@/views/um/admin/index')},
            {path: 'role', name: 'role', component: () => import('@/views/um/role/index')},
            {path: 'allocMenu', name: 'allocMenu', component: () => import('@/views/um/role/allocMenu')},
            {path: 'menu', name: 'menu', component: () => import('@/views/um/menu/index')}
        ]
    },

    {path: '*', name: 'notFound', component: () => import('@/views/not-found')},
];

const router = new VueRouter({
    routes
});

// 挂载路由的全局前置守卫
/**
 * to: 将要去访问的路径
 * from: 从哪个路径跳转而来
 * next 是一个函数
 * next() 放行   next('/login') 强制跳转
 */
router.beforeEach((to, from, next)=>{
    if(to.path === '/login') return next();
    // 获取token
    const tokenStr = window.sessionStorage.getItem(config.MANAGER_TOKEN);
    if(!tokenStr)  return next('/login');
    next();
});

export default router
