import ajax from './index'

// 1. 登录
export const checkLogin = (username, password)=> ajax('/api/auth/admin/login', {username, password}, 'post');

// 2. 退出登录
export const checkLogout = ()=> ajax('/api/auth/admin/logout');

// 3. 获取左侧的菜单
export const getLeftMenu = (pageNum, pageSize, parentId)=> ajax('/api/auth/admin/left_menu', {pageNum, pageSize, parentId}, 'post');

// 4. 添加角色
export const addRole = (queryList)=> ajax('/api/auth/admin/add_role', queryList, 'post');

// 5. 获取角色列表
export const getRoleList = (queryList)=> ajax('/api/auth/admin/role_list', queryList, 'post');

// 6. 根据id修改角色
export const editRole = (queryList)=> ajax('/api/auth/admin/edit_role', queryList, 'post');

// 7. 根据id删除角色
export const delRole = (queryList)=> ajax('/api/auth/admin/del_role', queryList, 'post');

// 8. 根据id删除角色
export const getLeftMenuAll = ()=> ajax('/api/auth/admin/all_menu_list');

// 9. 根据id获取角色的菜单
export const getRoleMenuWithId = (queryList)=> ajax('/api/auth/admin/get_menu_with_role_id', queryList, 'post');


// 10. 根据id修改角色的菜单
export const editRoleMenuWithId = (queryList)=> ajax('/api/auth/admin/edit_menu_with_role_id', queryList, 'post');



// 11. 获取管理员列表
