<template>
    <el-aside :width="isCollapsed ? '64px' : '200px'" style="background-color: rgb(238, 241, 246)">
        <scroll-bar>
            <el-menu
                    :collapse="isCollapsed"
                    :collapse-transition="false"
                    :default-active="$route.path"
                    active-text-color="#E9232C"
            >
                <template v-for="item in menuList">
                    <el-menu-item
                            :index="item.key"
                            v-if="item.children.length === 0"
                            @click="dealNavLink(item.key, item.id)"
                    >
                        <i :class="item.icon"></i>
                        <span slot="title">{{item.title}}</span>
                    </el-menu-item>
                    <el-submenu v-else :index="item.key">
                        <template slot="title">
                            <i :class="item.icon"></i>
                            <span slot="title">{{item.title}}</span>
                        </template>
                        <el-menu-item
                                v-for="subItem in item.children"
                                :index="subItem.key"
                                :key="subItem.key"
                                @click="dealNavLink(subItem.key, subItem.id)"
                        >
                            <template slot="title">
                                <i :class="subItem.icon"></i>
                                <span slot="title">{{subItem.title}}</span>
                            </template>
                        </el-menu-item>
                    </el-submenu>
                </template>
            </el-menu>
        </scroll-bar>
    </el-aside>
</template>

<script>
    import {getLeftMenuByRole} from './../../../api/adminApi'
    import config from './../../../config/config'
    import {mapState} from 'vuex'
    // import menuConfig from './../config/menuConfig'
    import ScrollBar from './../../../components/scrollBar/index'

    export default {
        name: "left-aside",
        data(){
           return {
               // menuList: menuConfig
               menuList: null
           }
        },
        created(){
             this.getLeftMenu();
        },
        components: {
            ScrollBar
        },
        computed: {
            ...mapState(["isCollapsed"]),
        },
        methods: {
            dealNavLink(activePath, activeId){
                // 路由跳转
                this.$router.push(activePath);
            },
            getLeftMenu(){
                let  roleId = JSON.parse(window.sessionStorage.getItem(config.MANAGER_USER)).roleid;
                let roleIdArr = [];
                if(roleId){
                    roleIdArr = roleId.split(',');
                }
                getLeftMenuByRole({roleIdArr}).then((response)=>{
                    if(response.status === 1){
                        this.menuList = response.data;
                    }
                })
            }
        }
    }
</script>

<style scoped lang="less">
  i{
      font-size: 20px;
      margin-right: 5px;
  }
</style>
