<template>
    <el-header>
         <div>
             <img src="./../../../assets/images/logo.png" alt="" width="100">
             <h2>撩课-电商后台管理系统</h2>
         </div>
        <el-button type="danger" @click="logout">退出</el-button>
    </el-header>
</template>

<script>
    import {checkLogout} from './../../../api/adminApi'
    import config from './../../../config/config'
    export default {
        name: "nav-bar",
        methods: {
            async logout(){
                const result = await checkLogout();
                this.$message({
                    message: result.msg,
                    type: 'success'
                });
                window.sessionStorage.removeItem(config.MANAGER_TOKEN);
                window.sessionStorage.removeItem(config.MANAGER_USER);
                this.$router.push('/login');
            }
        }
    }
</script>

<style scoped lang="less">
   .el-header{
       border-bottom: 1px solid #e6e6e6;
       display: flex;
       justify-content: space-between;
       align-items: center;
       >div{
           display: flex;
           align-items: center;
           margin-left: 15px;
           h2{
               margin-left: 20px;
           }
       }
   }
</style>
