<template>
    <div class="scroll-container" ref="scrollContainer" @wheel.prevent="handleScroll">
        <div class="scroll-wrapper" ref="scrollWrapper" :style="{top: top + 'px'}">
            <slot></slot>
        </div>
    </div>
</template>

<script>
    const delta = 30;
    export default {
        name: "index",
        data(){
           return {
               top: 0
           }
        },
        methods: {
            handleScroll(e){
                // 1. 获取两个元素
                const containerOffsetHeight = this.$refs.scrollContainer.offsetHeight;
                const wrapperOffsetHeight = this.$refs.scrollWrapper.offsetHeight;
                console.log(containerOffsetHeight, wrapperOffsetHeight);
                console.log(e);
                // 2. 取出滚动的范围
                const yDelta = e.wheelDeltaY || -e.deltaY * 10;
                // 3. 判断
                if(yDelta > 0){
                    this.top = Math.min(0, this.top + yDelta);
                }else {
                    if(containerOffsetHeight - delta  < wrapperOffsetHeight){
                         if(this.top < -(wrapperOffsetHeight - containerOffsetHeight + delta)){
                            this.top = this.top;
                         }else {
                             this.top = Math.max(
                                 this.top + yDelta,
                                 containerOffsetHeight - wrapperOffsetHeight - delta)
                         }
                    }else {
                        this.top = 0;
                    }
                }
            }
        }
    }
</script>

<style scoped lang="less">
   .scroll-container{
       position: relative;
       width: 100%;
       height: 100%;
       overflow-x: hidden;
       overflow-y: hidden;
       .scroll-wrapper{
           position: absolute;
           width: 100%;
       }
   }
</style>
